;\n  .weather-state {\n    font-weight: 600;\n    font-size: 24px;\n  }\n\n  .weather-place {\n    font-size: 34px;\n    font-weight: 400;\n  }\n}\n\n.temp-info2 {\n  display: flex;\n  align-items: center;\n  gap: 30px;\n\n  .tempVal {\n    font-size: 98px;\n    font-family: 'Varela Round', sans-serif;\n\n    p {\n      display: flex;\n    }\n\n    span {\n      font-size: 24px;\n    }\n  }\n\n  .line {\n    border: 1px solid #ffff;\n    height: 64px;\n    border-radius: 15px;\n  }\n\n  .weather-conditions {\n    margin-left: 5px;\n    font-size: 18px;\n    font-weight: 200;\n\n    p:first-child {\n      display: flex;\n    }\n\n    .scale {\n      font-size: 12px;\n    }\n  }\n}\n\n.other-content {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-end;\n  width: calc(100% - 760px);\n\n  .place {\n    position: absolute;\n    background-image: url(../assets/place.svg);\n    top: 55px;\n    margin-left: 20px;\n    width: 16px;\n    height: 20px;\n    background-repeat: no-repeat;\n  }\n\n  #search {\n    padding: 16px;\n    padding-left: 65px;\n    border: 1px solid #dfe1e5;\n    border-radius: 15px;\n    width: 220px;\n    margin-top: 40px;\n    margin-right: 50px;\n    align-self: flex-start;\n\n    &::-webkit-search-cancel-button {\n      position: relative;\n      -webkit-appearance: none;\n      top: 2px;\n      left: 4px;\n      height: 25px;\n      width: 25px;\n      background-image: url(../assets/delete.svg);\n      background-repeat: no-repeat;\n    }\n\n    &:focus {\n      color: #383838;\n    }\n\n    &:focus,\n    &:hover {\n      border: 1px solid white;\n      outline: none;\n      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;\n    }\n  }\n}\n\n.change-measure {\n  background-color: rgba($color: white, $alpha: 0.1);\n  margin-left: 30rem;\n  margin-bottom: 10px;\n  padding: 18px;\n  padding-bottom: 20px;\n  padding-right: 20px;\n  border: 2px solid #ffff;\n  border-radius: 100%;\n  cursor: pointer;\n\n  .scale {\n    width: 24px;\n    height: 22px;\n    font-family: 'Varela Round', sans-serif;\n    font-size: 20px;\n    color: #ffff;\n  }\n}\n\n.knobs,\n.layer {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n}\n\n.button {\n  margin-left: 28rem;\n  margin-bottom: 10px;\n  font-family: 'Varela Round', sans-serif;\n  position: relative;\n  width: 74px;\n  height: 42px;\n}\n\n.button,\n.button .layer {\n  border-radius: 100px;\n}\n\n.button.b2 {\n  border-radius: 2px;\n}\n\n.checkbox {\n  position: relative;\n  width: 100%;\n  height: 100%;\n  padding: 0;\n  margin: 0;\n  opacity: 0;\n  cursor: pointer;\n  z-index: 3;\n}\n\n.knobs {\n  z-index: 2;\n}\n\n.layer {\n  width: 100%;\n  background-color: rgba(255, 255, 255, 0.6);\n  transition: 0.3s ease all;\n  z-index: 1;\n}\n\n#button-1 .knobs:before {\n  content: '°F';\n  position: absolute;\n  top: 4px;\n  left: 4px;\n  width: 20px;\n  height: 10px;\n  color: #000;\n  font-size: 10px;\n  font-weight: bold;\n  text-align: center;\n  line-height: 1;\n  padding: 12px 7px;\n  background-color: #fff;\n  border-radius: 50%;\n  transition: 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;\n}\n\n#button-1 .checkbox:checked + .knobs:before {\n  color: #000;\n  content: '°C';\n  left: 36px;\n  background-color: #fff;\n}\n\n#button-1 .checkbox:checked ~ .layer {\n  background-color: rgba(255, 255, 255, 0.4);\n}\n\n#button-1 .knobs,\n#button-1 .knobs:before,\n#button-1 .layer {\n  transition: 0.3s ease all;\n}\n\n.forecast {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-self: center;\n  width: 502px;\n  height: 360px;\n  background-color: #fff;\n  box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.25);\n  border-radius: 30px;\n  margin-top: 20%;\n}\n\n.week {\n  display: flex;\n  flex-direction: column;\n  justify-items: center;\n  gap: 35px;\n\n  .col1,\n  .col2 {\n    display: flex;\n    justify-content: space-evenly;\n  }\n\n  .day {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 4px;\n    width: 64px;\n    height: 88px;\n    padding: 10px;\n    font-family: 'Inter', sans-serif;\n    font-weight: 200;\n  }\n}\n\n.loading-screen {\n  display: none;\n  align-items: center;\n  justify-content: center;\n  z-index: 3;\n  position: fixed;\n  width: 100%;\n  height: 100vh;\n  background-color: rgba($color: #000000, $alpha: 0.5);\n\n  .spinner {\n    z-index: 4;\n    .dot {\n      display: inline-block;\n      width: 25px;\n      height: 25px;\n      border-radius: 50%;\n      margin: 3px;\n      background-color: #ffffff;\n    }\n    .dot:nth-last-child(1) {\n      animation: jumpingAnimation 0.7s 0.2s ease-in infinite;\n    }\n    .dot:nth-last-child(2) {\n      animation: jumpingAnimation 0.7s 0.3s ease-in infinite;\n    }\n    .dot:nth-last-child(3) {\n      animation: jumpingAnimation 0.7s 0.4s ease-in infinite;\n    }\n  }\n}\n\n@keyframes jumpingAnimation {\n  0% {\n    transform: translate3d(0, 0, 0);\n  }\n  50% {\n    transform: translate3d(0, 15px, 0);\n  }\n  100% {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@media screen and (max-width: 1150px) {\n  body {\n    flex-direction: column;\n  }\n\n  .side-page {\n    height: 145vh;\n    width: 170vw;\n    background-size: cover;\n    background-repeat: no-repeat;\n  }\n\n  .other-content {\n    display: block;\n  }\n\n  .search {\n    position: absolute;\n    top: 0;\n    margin-left: 100vw;\n\n    #search {\n      margin-top: 105px;\n    }\n\n    .place {\n      top: 120px;\n    }\n  }\n\n  .forecast {\n    display: none;\n  }\n\n  .loading-screen {\n    height: 145vh;\n  }\n}\n"],"sourceRoot":""}]);
